@import './navbar.scss';
@import './intro.scss';
@import './about.scss';
@import './projects.scss';
@import './contact.scss';
@import './tech.scss';



body{
  font-family: Helvetica;
  h1{
    font-size: 45px;
  }
  a:hover{
    text-decoration: none;
  }
}

#root {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

ul li{
  list-style: none;
}

@media screen and (max-width: 400px) {
  body{
    h1{
      font-size: 32px;
    }


  }
}

